.mainDiv {
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 1s;
  /* gap: 10px; */
}

@media screen and (max-width: 600px) {
  .mainDiv {
    display: flex;
    transition: 2s;
    margin-top: 80px;
    justify-content: center;
    align-items: center;
  }
}

.input-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 15px;
  position: relative;
}

.input {
  border-style: none;
  height: 50px;
  width: 50px;
  padding: 5px 8px;
  outline: none;
  border-radius: 25px;
  transition: 0.5s ease-in-out;
  background-color: #7e4fd4;
  box-shadow: 0px 0px 3px #f3f3f3;
  padding-right: 45px;
  color: #fff;
}

.input::placeholder,
.input {
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
  font-size: 17px;
}

.input::placeholder {
  color: #8f8f8f;
}

.icon {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  right: 0px;
  cursor: pointer;
  width: 50px;
  height: 50px;
  outline: none;
  border-style: none;
  border-radius: 50%;
  pointer-events: painted;
  background-color: transparent;
  transition: 0.2s linear;
}

.icon:focus ~ .input,
.input:focus {
  box-shadow: none;
  width: 240px;
  border-radius: 0px;
  background-color: transparent;
  border-bottom: 3px solid #7e4fd4;
  transition: all 500ms cubic-bezier(0, 0.11, 0.35, 2);
}

.form {
  display: flex;
  position: fixed;
  top: 10px;
  right: 20px;
  z-index: 10;

  width: max-content;
  height: min-content;
}
